import { connect } from "react-redux";

import React, { useState, useEffect } from "react";
import NotificationAlert from "react-notification-alert";
// reactstrap components
import {
  Button,
  Card,
  CardHeader,
  CardBody,
  CardFooter,
  CardText,
  FormGroup,
  Form,
  Input,
  Row,
  Table,
  Col, 
} from "reactstrap";
 
const Wallet = (props) => {
  const notificationAlertRef = React.useRef(null);
  const showNotify = async (title, text, type = "success", place = 'tr', autoDismiss = 5) => {
    const options = {
      place: place,
      message: (
        <div><div>{title}</div><div>{text}</div></div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: autoDismiss,
    };
    notificationAlertRef.current.notificationAlert(options);
  }
  const { apiConfig, ApiCall } = global;
  const baseTokenSymbol = 'ETH';
  const explorerURL = 'https://etherscan.com/';
  const [list, setList] = useState([]);  
  const [data, setData] = useState({name:'',private:''});
  const removeWallet = async (data)=>{
    try {
      const response = await ApiCall(
        apiConfig.wallet_del.url,
        apiConfig.wallet_del.method,
        props.credential.loginToken,
        data
      );
      if(response.data){
        showNotify(response.data.message,'');
        setList(response.data.data);
      }
    } catch (error) {
      if (error.response) showNotify(error.response.data.message, '', 'danger');
      else if (error.request) showNotify("Request failed", '', 'danger');
      else showNotify("Something went wrong", '', 'danger');
    }
  }
  const addWallet = async () => {
    if (!data.name) {
      showNotify('Please input wallet name.','', 'danger');
      return;
    }
    if (!data.private) {
      showNotify('Please input private key.','', 'danger');
      return;
    }
    const sendData = JSON.parse(JSON.stringify(data));
    sendData.bcrypt = data.private;
    try {
      const response = await ApiCall(
        apiConfig.wallet_add.url,
        apiConfig.wallet_add.method,
        props.credential.loginToken,
        sendData,
      );
      if(response.data){
        setData({name:'',private:''});
        showNotify(response.data.message,'');
        setList(response.data.data);
      }
    } catch (error) {
      if (error.response) showNotify(error.response.data.message, '', 'danger');
      else if (error.request) showNotify("Request failed", '', 'danger');
      else showNotify("Something went wrong", '', 'danger');
    }
  };
  useEffect(() => {
    (async () => {
      try {
        const response = await ApiCall(
          apiConfig.wallet_list.url,
          apiConfig.wallet_list.method,
          props.credential.loginToken
        );
        if(response.data) setList(response.data.data);
      } catch (error) {
        if (error.response) showNotify(error.response.data.message, '', 'danger');
        else if (error.request) showNotify("Request failed", '', 'danger');
        else showNotify("Something went wrong", '', 'danger');
      }
    })();
  }, []);
  return (
    <>
      <div className="rna-container">
        <NotificationAlert ref={notificationAlertRef} />
      </div>
      <div className="content">
        <Row>
          <Col md="12">
            <Card>
              <CardHeader>
                <h5 className="title">Manage Wallet(Remember turn off Cleaner!!!)</h5>
              </CardHeader>
              <CardBody>
                <Row>
                  <Col className="pr-md-1" md="4">
                    <label>Wallet name</label>
                    <Input
                      type="text"
                      value={data.name}
                      onChange={(e) => setData({...data,name:e.target.value})}
                    />
                  </Col>
                  <Col className="pr-md-1" md="4">
                    <label>Privatekey</label>
                    <Input
                      type="password"
                      value={data.private}
                      onChange={(e) => setData({...data,private:e.target.value})}
                    />
                  </Col>
                  <Col className="pr-md-1" md="4">
                    <Button
                      className="btn-fill"
                      color="primary"
                      type="button"
                      onClick={addWallet}
                    >
                      Add
                    </Button>
                  </Col>
                </Row>
                </CardBody>
                <CardBody>
                  <Table responsive>
                    <thead className="text-primary">
                      <tr>
                        <th>Wallet Name</th>
                        <th>Address</th>
                        <th className="text-left">Actions</th>
                      </tr>
                    </thead>
                    <tbody>
                      {
                        list.map((item, key) => {
                          return (
                            <tr key={key}>
                              <td>{item.name}</td>
                              <td><a href={`${explorerURL}/address/${item.public}`} target="_blank">{item.public}</a></td>
                              <td className="text-left">
                                <Button className="btn-link" color="danger" size="sm" onClick={() => removeWallet(item)} title="Delete">
                                  <i className="tim-icons icon-simple-remove" />
                                </Button>
                              </td>
                            </tr>
                          );
                        })
                      }
                    </tbody>
                  </Table>
                </CardBody>
            </Card>
          </Col>
        </Row>
      </div>
    </>
  );
};

const mapStateToProps = (state) => {
  const { LoginReducer } = state;
  return { credential: LoginReducer };
};

export default connect(mapStateToProps)(Wallet);
